/* ----------------------------------- COLORS ----------------------------------- */
/* ----------------------------------- FONTS ----------------------------------- */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  font-family: "Open Sans", sans-serif;
  color: hsla(0deg, 0%, 100%, 0.85);
  font-weight: 400;
}

body {
  background-color: hsl(257deg, 40%, 49%);
  background-image: url(../img/bg-mobile.svg);
  background-size: 100%;
  background-repeat: no-repeat;
  background-position: 0 -10px;
  display: flex;
  justify-content: center;
}

.page-container {
  width: calc(100% - 70px);
  margin-top: 0.6rem;
}
.page-container header {
  width: 100%;
  padding: 1.2rem 0;
  margin-bottom: 1.7rem;
}
.page-container header img {
  width: 40%;
  max-width: 200px;
}
.page-container main {
  text-align: center;
  display: flex;
  flex-direction: column;
  margin-bottom: 4.5rem;
}
.page-container main .hero {
  width: 100%;
  margin-bottom: 3.8rem;
  object-fit: contain;
}
.page-container main .content-container h1 {
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  font-size: 1.5rem;
  color: hsl(0deg, 0%, 100%);
  margin-bottom: 1rem;
}
.page-container main .content-container p {
  line-height: 1.5rem;
  margin-bottom: 2rem;
}
.page-container main .content-container .button {
  padding: 0.7rem 4.9rem;
  background-color: hsl(0deg, 0%, 100%);
  border-radius: 50px;
  font-size: 0.8rem;
  font-weight: 600;
  text-decoration: none;
  color: #846dc0;
  box-shadow: 0 2px 5px 0px hsla(0deg, 0%, 0%, 0.3);
  transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out;
  cursor: pointer;
}
.page-container main .content-container .button:hover {
  background-color: hsl(300deg, 69%, 71%);
  color: hsl(0deg, 0%, 100%);
}
.page-container footer {
  margin-bottom: 2.5rem;
  display: flex;
  justify-content: center;
  position: relative;
  width: 100%;
}
.page-container footer ul {
  list-style-type: none;
  display: flex;
  gap: 0.7rem;
}
.page-container footer ul li {
  font-size: 1rem;
  width: 1.8rem;
  height: 1.8rem;
  border: 1px solid hsl(0deg, 0%, 100%);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: hsl(0deg, 0%, 100%);
  cursor: pointer;
  transition: background-color 0.2s ease-in-out, color 0.1s ease-in-out;
}
.page-container footer ul li:hover {
  border: 1px solid hsl(300deg, 69%, 71%);
  color: hsl(300deg, 69%, 71%);
}

@media screen and (min-width: 768px) {
  body {
    height: 100vh;
    background-image: url(../img/bg-desktop.svg);
    background-size: 92% 105%;
  }
  .page-container {
    max-width: 1295px;
    margin-top: 2.2rem;
  }
  .page-container header {
    margin-bottom: 0rem;
  }
  .page-container main {
    flex-direction: row;
    margin-bottom: 1.3rem;
    text-align: left;
  }
  .page-container main .hero {
    width: 55%;
    margin-bottom: 0;
  }
  .page-container main .content-container {
    padding: 1rem;
  }
  .page-container main .content-container h1 {
    font-size: 2rem;
  }
  .page-container main .content-container p {
    font-size: 1rem;
    margin-bottom: 2.5rem;
  }
  .page-container main .content-container .button {
    padding: 0.8rem 4rem;
    font-size: 1.1rem;
    font-weight: 400;
    color: #523c8b;
    box-shadow: 2px 2px 7px 2px hsla(0deg, 0%, 0%, 0.3);
  }
  .page-container footer ul {
    position: absolute;
    left: 0;
  }
  .page-container footer ul li {
    font-size: 1.2rem;
    width: 2.6rem;
    height: 2.6rem;
    margin-right: 0.2rem;
  }
}
@media screen and (min-width: 900px) {
  .page-container header {
    margin-bottom: 4rem;
  }
  .page-container main .content-container {
    padding: 3.2rem 0.5rem 3.2rem 3.2rem;
  }
  .page-container main .content-container h1 {
    font-size: 2.5rem;
  }
  .page-container main .content-container p {
    font-size: 1.15rem;
  }
  .page-container main .content-container .button {
    padding: 1rem 4rem;
    font-size: 1.2rem;
  }
  .page-container footer ul {
    position: absolute;
    right: 0;
    left: auto;
  }
}